</template>

<style scoped>
  .container-narrow{
    margin: 0 auto;
    max-width: 90%;
  }
  .masthead{
    width: 100%;
    margin-top: 5px;
  }
  .cat{
    width: 130px;
  }
  .num{
    width: 60px;
  }
  .fun-btn-group{
    margin-top: 15px;
    margin-bottom: 15px;
  }
</style>

<script>
import Editormd from '@/components/common/Editormd'
import JsonToTable from '@/components/common/JsonToTable'
import JsonBeautify from '@/components/common/JsonBeautify'
import TemplateList from '@/components/page/edit/TemplateList'
import HistoryVersion from '@/components/page/edit/HistoryVersion'
// var $s = require('scriptjs')
var $ = require('jquery')

export default {

  data () {
    return {
      currentDate: new Date(),
      itemList: {},
      content: '',
      title: '',
      item_id: 0,
      cat2: {},
      cat_id2: '',
      cat3: {},
      cat_id3: '',
      s_number: '',
      page_id: '',
      copy_page_id: ''
    }
  },
  components: {
    Editormd,
    JsonToTable,
    JsonBeautify,
    TemplateList,
    HistoryVersion
  },
  methods: {
    // 
    get_page_content (page_id) {
      if (!page_id) {
        page_id = this.page_id
      }
      var that = this
      var url = this.DocConfig.server + '/admin/page'
      var params = new URLSearchParams()
      params.append('page_id', page_id)
      that.$http.post(url, params)
        .then(function (response) {
          if (response.data.status === 200) {
            // that.$message.success("");
            that.content = response.data.data.pagecontent
            that.title = response.data.data.pagetitle
            that.item_id = response.data.data.itemid
            that.s_number = response.data.data.snumber
          } else {
            that.$alert(response.data.message)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    // 
    get_cat2 (item_id) {
      var that = this
      var url = this.DocConfig.server + '/secondCatList'
      var params = new URLSearchParams()
      params.append('item_id', item_id)
      that.$http.post(url, params)
        .then(function (response) {
          if (response.data.status === 200) {
            // that.$message.success("");
            that.cat2 = response.data.data
            that.get_default_cat()
            console.log(that.cat2)
          } else {
            that.$alert(response.data.message)
          }
        })
    },
    // 
    get_cat3 (cat_id, callback) {
      var that = this
      var url = this.DocConfig.server + '/childCatList'
      var params = new URLSearchParams()
      params.append('cat_id', cat_id)
      that.$http.post(url, params)
        .then(function (response) {
          if (response.data.status === 200) {
            // that.$message.success("")
            that.cat_id3 = ''
            that.cat3 = response.data.data
            if (callback) {
              callback()
            }
          } else {
            that.$alert(response.data.message)
          }
        })
    },
    // 
    get_default_cat () {
      var that = this
      var url = this.DocConfig.server + '/getDefaultCat'
      var params = new URLSearchParams()
      params.append('page_id', this.page_id)
      params.append('copy_page_id', this.copy_page_id)
      that.$http.post(url, params)
        .then(function (response) {
          if (response.data.status === 200) {
            // that.$message.success("")
            var json = response.data.data
            that.cat_id2 = json.default_cat_id2
            that.get_cat3(json.default_cat_id2, function () {
              that.cat_id3 = json.default_cat_id3
            })
          } else {
            that.$alert(response.data.message)
          }
        })
    },
    // is_cover()
    insertValue (value, is_cover) {
      if (value) {
        const childRef = this.$refs.Editormd // 
        if (is_cover) {
          // 
          childRef.clear()
        };
        childRef.insertValue(value) // 
      };
    },
    // api
    insert_api_template () {
      this.insertValue(this.$refs.api_doc_templ.innerHTML)
    },
    // 
    insert_database_template () {
      this.insertValue(this.$refs.database_doc_templ.innerHTML)
    },
    // json
    ShowJsonToTable () {
      const childRef = this.$refs.JsonToTable // 
      childRef.dialogFormVisible = true
    },
    // json
    ShowJsonBeautify () {
      const childRef = this.$refs.JsonBeautify // 
      childRef.dialogFormVisible = true
    },
    ShowRunApi () {
      window.open('http://runapi.showdoc.cc/')
    },
    // 
    ShowTemplateList () {
      const childRef = this.$refs.TemplateList // 
      childRef.show()
    },
    // 
    ShowHistoryVersion () {
      const childRef = this.$refs.HistoryVersion // 
      childRef.show()
    },
    save () {
      var that = this
      var loading = that.$loading()
      const childRef = this.$refs.Editormd
      var content = childRef.getMarkdown()
      var cat_id = 0
      if (that.cat_id2 > 0) {
        cat_id = that.cat_id2
      };
      if (that.cat_id3 > 0) {
        cat_id = that.cat_id3
      };
      var item_id = that.$route.params.item_id
      var page_id = that.$route.params.page_id
      var url = this.DocConfig.server + '/page/save'
      var params = new URLSearchParams()
      params.append('page_id', page_id)
      params.append('item_id', item_id)
      params.append('s_number', that.s_number)
      params.append('page_title', that.title)
      params.append('page_content', content)
      params.append('cat_id', cat_id)
      that.$http.post(url, params)
        .then(function (response) {
          loading.close()
          if (response.data.status === 200) {
            // that.$message.success("")
            localStorage.removeItem('page_content')
            that.$router.push({ path: '/' + item_id, query: { page_id: response.data.data.page_id } })
          } else {
            that.$alert(response.data.message)
          }
        })
      // 
      setTimeout(() => {
        loading.close()
      }, 20000)
    },
    goback () {
      var url = '/' + this.$route.params.item_id
      this.$router.push({ path: url, query: { page_id: this.$route.params.page_id } })
    },
    dropdown_callback (data) {
      if (data) {
        data()
      }
    },
    // 
    save_to_template () {
      var that = this
      const childRef = this.$refs.Editormd
      var content = childRef.getMarkdown()
      this.$prompt(that.$t('save_templ_title'), ' ', {
      }).then(function (data) {
        var url = this.DocConfig.server + '/template/save'
        var params = new URLSearchParams()
        params.append('template_title', data.value)
        params.append('template_content', content)
        that.$http.post(url, params)
          .then(function (response) {
            if (response.data.status === 200) {
              that.$alert(that.$t('save_templ_text'))
            } else {
              that.$alert(response.data.message)
            }
          })
      })
    },
    /**  **/
    on_paste () {
      var that = this
      var url = this.DocConfig.server + '/upload'
      document.addEventListener('paste', function (e) {
        var clipboard = e.clipboardData
        for (var i = 0, len = clipboard.items.length; i < len; i++) {
          if (clipboard.items[i].kind === 'file' || clipboard.items[i].type.indexOf('image') > -1) {
            var imageFile = clipboard.items[i].getAsFile()
            var form = new FormData()
            form.append('t', 'ajax-uploadpic')
            form.append('editormd-image-file', imageFile)
            var loading = ''
            var callback = function (type, data) {
              type = type || 'before'
              switch (type) {
                // 
                case 'before':
                  loading = that.$loading()
                  break
                  // 
                case 'error':
                  loading.close()
                  that.$alert('')
                  break
                  // 
                case 'success':
                  loading.close()
                  if (data.success === 1) {
                    var value = '![](' + data.url + ')'
                    that.insertValue(value)
                  } else {
                    that.$alert('')
                  }
                  break
              }
              console.log(type)
            }
            $.ajax({
              url: url,
              type: 'POST',
              dataType: 'json',
              data: form,
              processData: false,
              contentType: false,
              beforeSend: function () {
                // eslint-disable-next-line standard/no-callback-literal
                callback('before')
              },
              error: function () {
                // eslint-disable-next-line standard/no-callback-literal
                callback('error')
              },
              success: function (data) {
                // eslint-disable-next-line standard/no-callback-literal
                callback('success', data)
              }
            })
            e.preventDefault()
          }
        }
      })
    }
  },
  mounted () {
    var that = this
    this.page_id = this.$route.params.page_id
    this.copy_page_id = this.$route.query.copy_page_id ? this.$route.query.copy_page_id : ''
    if (this.copy_page_id > 0) {
      this.get_page_content(this.copy_page_id)
    } else if (this.page_id > 0) {
      this.get_page_content(this.page_id)
    } else {
      this.item_id = this.$route.params.item_id
      this.content = this.$t('welcome_use_showdoc')
    }
    this.get_cat2(this.$route.params.item_id)
    that.on_paste()
    document.onkeydown = function (e) { //  
      var keyNum = window.event ? e.keyCode : e.which // 
      if (keyNum === 83 && e.ctrlKey) { // Ctrl +S 
        that.save()
        e.preventDefault()
      };
    }
  }
}
</script>
