// actionmutationmutationaction

export default {
  incrementAsync ({ commit }) {
    setTimeout(() => {
      commit('increment')
    }, 1000)
  },
  changeItemInfo (ctx, val) {
    // console.log(val)  //valdispatch
    // console.log(ctx)  //ctx
    ctx.commit('changeItemInfo', val)// commitmutation
  }
}
